import { useState } from 'react';
// import ReactDOM from 'react-router-dom';


//Denne kode importerer useState-funktionen fra React og eksporterer 
//en funktion kaldet MyForm. MyForm er en React-komponent, 
//som returnerer en HTML-formular med fire inputfelter
//(firstName, lastName, phone og message) og en submit-knap.

export default function MyForm() {
    const [firstName, setFirstName] = useState("");
    const [lastName, setLastName] = useState("");
    const [message, setMessage] = useState("");
    const [phone, setPhone] = useState("");

    //vi har lavet 4 variabler, konstanter med fire useState-hooks 
    //tomme strenge som startvrdi- fordi nr en bruger taster vrdier i 
    //inputfelterne, vil hooks'ene blive opdateret med den aktuelle vrdi.

    const handleSubmit = (event) => {
        event.preventDefault();
        alert(`Tak fordi du kontaktede os ${firstName}, du vil modtage en beskrftelsesmeddelelse p mail inde for 15 min.`)
    }

    //Nr send knappen bliver klikket p, kalder handleSubmit-funktionen, 
    //som viser en alert-besked med en bekrftelsesmeddelelse, der indeholder 
    //fornavnet p den person, der udfyldte formularen.

    return (
        <form onSubmit={handleSubmit}>

            <label>Fornavn
                <input
                    type="text"
                    value={firstName}
                    placeholder="Skriv dit fornavn her"
                    //hvis der bliver skrevet i feltet, s vil vrdien setFirstName blive opdateret
                    onChange={(e) => setFirstName(e.target.value)}
                    required
                />
            </label>

            <label>Efternavn<span className="notrequired">ikke pkrvet</span>
                <input
                    type="text"
                    value={lastName}
                    placeholder="Skriv dit efternavn her"
                    onChange={(e) => setLastName(e.target.value)}
                />
            </label>

            <label>Telefonnummer
                <input
                    type="tel"
                    value={phone}
                    placeholder="Skriv dit telefonnummer her"
                    onChange={(e) => setPhone(e.target.value)}
                    required
                />
            </label>

            <label>Besked
                <textarea
                    value={message}
                    placeholder="Hvad har du lyst til at vi skal tale om?"
                    onChange={(e) => setMessage(e.target.value)}
                    required
                />
            </label>

            <input type="submit" className='pinkbutton' />
        </form>
    )
}
