import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';



//Denne kode importerer nogle moduler fra React biblioteket og 
//nogle andre filer ('./index.css', './App', og './reportWebVitals').

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

//Derefter opretter koden en "root" node ved hjlp af 
//ReactDOM.createRoot-metoden, som vil fungere som det verste niveau 
//af vores React-komponenttr og definere, hvor vores React-app 
//vil blive monteret i HTML DOM'en.

//Derefter renderes vores App-komponent ved hjlp af ReactDOM's .render() metode,
// som tager et React element (i dette tilflde vores App-komponent) 
//og en ml-node (i dette tilflde 'root' node).


//Til sidst kaldes reportWebVitals-metoden, som vil starte mling af 
//ydeevne i vores app, hvis vi nsker at mle ydeevnen og logge resultaterne.


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
